import { Component, ElementRef, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { MatRow, MatTableDataSource } from '@angular/material/table';
import { ViewportService } from '@core/services/viewport.service';
import { debounceTime, distinctUntilChanged, map, Observable, Subject, switchMap, takeUntil, tap } from 'rxjs';
import { FiltersListingsComponent } from '../../components/filters-listings/filters-listings.component';
import { MatDialog } from '@angular/material/dialog';
import { AgentHubFacade } from '@feature/agent-hub/agent-hub.facade';
import { MatPaginator, MatPaginatorIntl, PageEvent } from '@angular/material/paginator';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { DestroyService } from '@shared/services/destroy.service';
import { Ad, AgentPageMetaData, GetAdStatusListResponse, PageMetaData, SubscriptionInfo } from '@core/models';
import { APP_ROUTE } from '@core/constants';
import { AdStatusFormatPipe } from '@shared/pipes';
import { LimitReachedDialogComponent } from './limit-reached-dialog/limit-reached-dialog.component';
import { PlaceAdFacade } from '@feature/place-ad/place-ad.facade';
import { AuthService } from '@core/data-access/services/auth.service';
import { LIMITED_COUPON_CODE } from '@feature/place-ad/place-ad.constant';
import { SubscriptionTier } from '@core/enums/profile.enum';
import { MyProfileFacade } from '@feature/my-profile/my-profile.facade';
import { SubscriptionOfferDialogComponent } from '@feature/my-profile/v2/components/subscription-offer-dialog/subscription-offer-dialog.component';

class MyCustomPaginatorIntl extends MatPaginatorIntl {
	override getRangeLabel = (page: number, pageSize: number, length: number) => {
		if (length === 0 || pageSize === 0) {
			return `Page 1 of ${length}`;
		}

		length = Math.max(length, 0);
		return `Page ${page + 1} of ${Math.ceil(length / pageSize)}`;
	};
}

@Component({
    selector: 'findqo-listings',
    templateUrl: './listings.component.html',
    styleUrl: './listings.component.scss',
    providers: [AdStatusFormatPipe, DestroyService, { provide: MatPaginatorIntl, useValue: new MyCustomPaginatorIntl() }],
    standalone: false
})
export class ListingsComponent {
	constructor(
		public viewport: ViewportService,
		private dialog: MatDialog,
		private agentHubFacade: AgentHubFacade,
		private destory: DestroyService,
		private route: ActivatedRoute,
		private adStatusFormatPipe: AdStatusFormatPipe,
		private router: Router,
		private placeAdFacade: PlaceAdFacade,
		private auth: AuthService,
		private myProfileFacade: MyProfileFacade,
		private destroy: DestroyService
	) {
		this.usersMetaData$ = this.agentHubFacade.pageMetaData$;
		this.statusList$ = this.agentHubFacade.getAdsStatusList$();
		this.isLoading$ = this.agentHubFacade.isLoading$();
		this.subscription$ = this.myProfileFacade.getSubscriptionInfo$();
	}

	@ViewChild(MatPaginator, { static: true }) paginator!: MatPaginator;
	@ViewChildren(MatRow, { read: ElementRef }) rows!: QueryList<ElementRef<HTMLTableRowElement>>;

	readonly user: any = this.auth.user();
	readonly usersMetaData$: Observable<AgentPageMetaData>;
	readonly statusList$: Observable<GetAdStatusListResponse[]>;

	readonly isLoading$: Observable<boolean>;
	readonly pageSizeOptions = [25, 50, 100, 250, 500];

	hasPlanAdsLeft: boolean;
	adsCount: any;
	subscriptionInfo: SubscriptionInfo;
	dataSource = new MatTableDataSource<Ad>();
	selectedRowId = '';
	pageLabel = '';
	pageMetadata!: AgentPageMetaData;
	userList!: Ad[];
	selectedUser: Ad;
	isLoading = false;
	queryParams!: AgentPageMetaData;
	searchValue = '';
	searchSubject: Subject<string> = new Subject<string>();
	columnType: { colName?: string; direction?: string };
	displayColumns: string[] = ['listings', 'adTier', 'status', 'assignedTo', 'expiry', 'view'];
	filterCount = 0;

	noItems: string[] = [
		'Enjoy your First 2 Months of Property Listings for FREE',
		'Benefit from unlimited ad views and dedicated support',
		'Showcase your brand by advertising under your business name and brand logo on the ad list',
	];

	sortMap = {
		listings: {
			asc: 'date-oldest',
			desc: 'date-latest',
		},
	};

	subscription: SubscriptionInfo;
	subscription$: Observable<SubscriptionInfo>;

	onViewAd(ad): void {
		this.router.navigate([APP_ROUTE.agentHub.detail, ad?.id]);
	}

	onMenuSort(colName: string, sortDirection: string): void {
		const sortKey = sortDirection === 'clear' ? null : this.sortMap[colName]?.[sortDirection];

		this.router.navigate([], {
			relativeTo: this.route,
			queryParams: { sort: sortKey },
			queryParamsHandling: 'merge',
		});
	}

	onPlaceAd(): void {
		if (this.user.businessProfile.subscription.reference === SubscriptionTier.LIMITED_YEARLY) {
			this.placeAdFacade
				.getPaymentCount({
					userId: this.user?.id,
					sellerTypeId: '2',
					couponCode: LIMITED_COUPON_CODE,
				})
				.pipe()
				.subscribe((meta: PageMetaData) => {
					this.adsCount = +(meta?.total ?? 0);
					this.hasPlanAdsLeft = this.adsCount < 2;

					if (this.hasPlanAdsLeft) {
						this.router.navigate([APP_ROUTE.placeAdCategory], {
							queryParams: { sellerTypeId: 2 },
						});
					} else {
						this.dialog.open(LimitReachedDialogComponent, {
							disableClose: true,
							maxWidth: '500px',
							maxHeight: '354px',
						});
					}
				});

			return;
		}

		this.subscription$.pipe(takeUntil(this.destroy)).subscribe((subscription) => {
			this.subscription = subscription;
		});

		if (this.subscription.status === 'cancelled' && new Date(this.subscription.dates.expired) < new Date()) {
			this.dialog.open(SubscriptionOfferDialogComponent, {
				disableClose: true,
				width: '500px',
				height: '600px',
			});

			return;
		}

		this.router.navigate([APP_ROUTE.placeAdCategory], {
			queryParams: { sellerTypeId: 2 },
		});
	}

	onClearSearch(): void {
		this.agentHubFacade.setIsLoading(true);
		this.searchValue = '';
		this.router.navigate([], {
			relativeTo: this.route,
			queryParams: { search: null },
			queryParamsHandling: 'merge',
		});
	}

	onFilter(): void {
		this.agentHubFacade.loadAgentList();

		this.dialog.open(FiltersListingsComponent, {
			disableClose: true,
			width: '500px',
			height: '100%',
			panelClass: 'filter-form',
		});
	}

	onSearch(keyword: string): void {
		keyword = keyword.trim();

		this.searchSubject.next(keyword);
		this.searchValue = keyword;
	}

	getStatusClass(status: string): string {
		const formattedStatus = this.adStatusFormatPipe.transform(status);
		switch (formattedStatus) {
			case 'Live':
				return 'badge-live';
			case 'Payment Failed':
				return 'badge-payment-failed';
			case 'Expired':
				return 'badge-expired';
			case 'Edit Under Review':
				return 'badge-edit-under-review';
			case 'Draft':
				return 'badge-default';
			case 'Under Review':
				return 'badge-edit-under-review';
			case 'Payment Pending':
				return 'badge-edit-under-review';
			case 'Sold':
				return 'badge-sold';
			case 'Property Occupied':
				return 'badge-property-occupied';
			case 'Sale Agreed':
				return 'badge-sale-agreed';
			default:
				return 'badge-default';
		}
	}

	onPageChange(paginator: PageEvent, metaData: AgentPageMetaData | null): void {
		const currentQueryParams = this.route.snapshot.queryParams;

		this.router.navigate([APP_ROUTE.agentHub.dashboard, APP_ROUTE.agentHub.listings], {
			queryParams: {
				...currentQueryParams,
				page: paginator.pageIndex + 1,
				perPage: paginator.pageSize,
			},
			queryParamsHandling: 'merge',
		});
	}

	scrollToIndex(id: string): void {
		setTimeout(() => {
			this.selectedRowId = id;

			const ELEMENT = this.rows.find((row) => row.nativeElement.id === id);

			if (ELEMENT) {
				ELEMENT.nativeElement.scrollIntoView({
					block: 'center',
					behavior: 'auto',
				});
			}
		});
	}

	updatePaginator(metaData: PageMetaData): void {
		const startIndex = (Number.parseInt(metaData.currentPage) - 1) * Number.parseInt(metaData.perPage) + 1;
		const endIndex = Math.min(startIndex + Number.parseInt(metaData.perPage) - 1, Number.parseInt(metaData.total));
		this.pageLabel = `Showing ${startIndex} to ${endIndex} of ${metaData.total} item(s)`;
	}

	getTypeFromSort(sort: string): { colName: string; direction: 'asc' | 'desc' } | null {
		for (const [colName, directions] of Object.entries(this.sortMap)) {
			if (directions.asc === sort) {
				return { colName, direction: 'asc' };
			} else if (directions.desc === sort) {
				return { colName, direction: 'desc' };
			}
		}

		return null;
	}

	getFilterCount(): void {
		this.route.queryParams.subscribe((params) => {
			const filterKeys = ['aisleId', 'agentId', 'status', 'isFeatured'];

			this.filterCount = filterKeys.reduce((count, key) => {
				return params[key] ? count + 1 : count;
			}, 0);
		});
	}

	ngOnInit(): void {
		this.getFilterCount();

		this.searchSubject
			.pipe(
				debounceTime(1000),
				distinctUntilChanged(),
				tap((search) => {
					if (search?.length === 0) search = null;
					const currentQueryParams = this.route.snapshot.queryParams;

					this.router.navigate([APP_ROUTE.agentHub.dashboard, APP_ROUTE.agentHub.listings], {
						queryParams: {
							...currentQueryParams,
							page: 1,
							perPage: this.pageMetadata.perPage,
							search,
						},
						queryParamsHandling: 'merge',
					});
				}),
				takeUntil(this.destory)
			)
			.subscribe();
	}

	ngAfterViewInit(): void {
		this.dataSource.paginator = this.paginator;

		this.route.queryParams
			.pipe(
				switchMap((params: Params) => {
					return this.agentHubFacade.loadAgencyList$(params).pipe(
						map((response) => {
							return {
								...response,
								params,
							};
						})
					);
				})
			)
			.subscribe((response: any) => {
				this.dataSource = new MatTableDataSource(response.data);
				this.pageMetadata = response?.meta;
				this.queryParams = response?.params;
				this.updatePaginator(response?.meta);

				if (this.dataSource?.paginator) {
					this.dataSource.paginator.firstPage();
				}

				if (response?.params?.agencyId) {
					this.scrollToIndex(response.params.agencyId);
				} else {
					this.selectedRowId = '';
				}

				if (response?.params?.search) {
					this.searchValue = response?.params?.search ?? '';
				}

				if (response?.params?.sort) {
					this.columnType = this.getTypeFromSort(response?.params?.sort);
				} else {
					this.columnType = {};
				}

				this.agentHubFacade.setPageMetaData(response?.params);
			});
	}
}
